import "./App.css";

import React from "react";
import * as Constants from "./constants.js";

import {
  Aatrox,Akshan,Aphelios,Ashe,Brand,Diana,Draven,
  Fiddlesticks,Galio,Garen,Gragas,Gwen,Hecarim,
  Heimerdinger,Irelia,Ivern,Jax,Kalista,Karma,Kayle,
  Kennen,Khazix,Kled,LeeSin,Leona,Lucian,Lulu,Lux,
  MissFortune,Nautilus,Nidalee,Nocturne,Nunu,Olaf,
  Poppy,Pyke,Rakan,Rell,Riven,Sejuani,Senna,Sett,
  Soraka,Syndra,Teemo,Thresh,Tristana,Udyr,Varus,
  Vayne,Velkoz,Viego,Vladimir,Volibear,Yasuo,Ziggs,Zyra,
} from "./tft_data";

class Composition extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: null,
    };
  }

  render() {
    return (
      <>
        <div>
          <button
            onClick={() =>
              this.setState({
                value:
                  Constants.MetaComps[
                    Math.floor(Math.random() * Constants.MetaComps.length)
                  ],
              })
            }
          >
            Give Comp
          </button>
        </div>
        <div>
          <p>{this.state.value}</p>
        </div>
      </>
    );
  }
}

class ChampionSelector extends React.Component {
  render() {
    return (
      <>
        <img src={Aphelios} alt="Champion" />
        <img src={Akshan} alt="Champion" />
        <img src={Teemo} alt="Champion" />
        <img src={Diana} alt="Champion" />
        <img src={Ivern} alt="Champion" />
        <img src={Volibear} alt="Champion" />
        <img src={Fiddlesticks} alt="Champion" />
        <img src={Lulu} alt="Champion" />
      </>
    );
  }
}

function App() {
  return (
    <div>
      <h1>TFT Comps</h1>
      <Composition />
      <ChampionSelector />
    </div>
  );
}

export default App;
